/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import Edit from './edit'
import icon from './icon'

registerBlockType( 'gravityforms/mailchimp', {

	title:       'MailChimp',
	description: __( 'Embed a MailChimp signup form on your page.', 'gravityforms' ),
	category:    'embed',
	supports:    {
		customClassName: false,
		className:       false,
		html:            false,
	},
	attributes:  {
		blockID:          {
			type: 'string'
		},
		list:             {
			type: 'string'
		},
		orientation:      {
			type:    'string',
			default: 'vertical'
		},
		nameField:        {
			type:    'bool',
			default: true
		},
		doubleOptIn:      {
			type:    'bool',
			default: true
		},
		formTitle:        {
			type:    'string',
			default: 'Newsletter Signup'
		},
		formDescription:  {
			type: 'string'
		},
		submitText:       {
			type:    'string',
			default: 'Submit'
		},
		confirmationText: {
			type:    'string',
			default: __( 'Thank you for subscribing to our newsletter!', 'gravityforms' )
		},
		conditionalLogic: {
			type:    'object',
			default: {
				enabled:    false,
				actionType: 'show',
				logicType:  'all',
				rules:      [],
			},
		},
	},
	icon:        icon,

	edit: Edit,

	save() {
		return (
			null
		);
	},

} );
